<template>
  <div>
    <!-- tabbar -->
    <van-sticky>
      <van-nav-bar :title="titles[$route.name]">
        <!--   setting  -->
        <div v-if="$route.name !== 'Setting'" slot="left" @click="showSwitch">
          <van-icon name="exchange" color="#333" size="20" style="margin-right: 5px;" />
          <span>{{ currentWallet.name }}</span>
        </div>

        <!--  -->
        <div
          v-if="$route.name === 'WalletPortfolio'"
          slot="right"
          @click="() => { this.$router.push('/wallet-list') }"
        >
          <van-icon name="add-o" color="#333" size="20" />
        </div>
      </van-nav-bar>
    </van-sticky>

    <!--  -->
    <wallet-switch ref="wallet-switch"></wallet-switch>

    <keep-alive>
      <router-view></router-view>
    </keep-alive>

    <!-- tabbar -->
    <div style="height: 50px;"></div>
    <van-tabbar safe-area-inset-bottom active-color="#333" inactive-color="#999" route>
      <van-tabbar-item to="/" icon="paid"></van-tabbar-item>
      <van-tabbar-item to="/staking" icon="chart-trending-o"></van-tabbar-item>
      <van-tabbar-item to="/governance-list" icon="comment-o"></van-tabbar-item>
      <van-tabbar-item to="/setting" icon="setting-o"></van-tabbar-item>
    </van-tabbar>
  </div>
</template>


<script>
import baseMixin from "../../store/baseMixin";

export default {
  mixins: [baseMixin],
  data() {
    return {
      titles: {
        WalletPortfolio: "",
        Staking: "",
        Governances: "",
        Setting: ""
      }
    };
  },
  methods: {
    showSwitch() {
      this.$refs["wallet-switch"].switch();
    }
  }
};
</script>
